.popup__container {
  max-width: 430px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  align-self: center;
  position: relative;
}

@media screen and (max-width: 624px) {
  .popup__container {
    max-width: 282px;
    margin: 0 auto;
  }
}