.popup__input {
  max-width: 358px;
  height: 27px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;

  color: #000000;
}

@media screen and (max-width: 624px) {
  .popup__input {
    max-width: 238px;
  }
}