.popup__title {
max-width: 330px;
width: 100%;
font-family: 'Inter', Arial, sans-serif;
font-style: normal;
font-weight: 900;
font-size: 24px;
line-height: 29px;
color: #000000;
margin: 34px 64px 54px 36px;
}

@media screen and (max-width: 624px) {
  .popup__title {
    max-width: 238px;
    font-size: 18px;
    line-height: 22px;
    margin: 22px auto 75px;
  }
}